* {
  box-sizing: border-box;
}

.wrapper {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 1rem;
  background: rgb(139, 132, 132);
}

#editor {
  display:block;
  border-radius: 5px;
  border-style: inset;
  border-width: .3rem;
  border-style:inset;
  border-style: inset;
  border-color: #000;
  padding: 1rem;
  background-color: #000;
  color: #0f0;
  margin: auto;
  outline: none;
  resize: none;
}
#preview {
  display:block;
  border-radius: 5px;
  padding: 1rem;
  background-color: #eee;
  border: 1px solid #ddd;
  color: #000;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: auto;
  outline: none;
  resize: none;
  width: 100%;
  min-height: 100px;
}

.down {
  float: right;
}
.up {
  float: right;
  transform: rotate(90deg);
}

#preview img {
  max-width: 50px;
}
#preview a {
  text-decoration: none;
  font-weight: bold;
  color: blue;
}
#preview a:hover {
  text-decoration: none;
  font-weight: bold;
  color: dodgerblue;
}
#preview a::before {
  content: "**"
}
#preview a::after {
  content: "**"
}
#preview blockquote {
  background-color: rgba(200, 90, 90, .7);
  color: rgba(255, 255, 255, .8);
  padding: .1rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  border-left: rgba(200, 90, 90, .9) solid 10px;
}

button {
  padding: .5rem;
  border-radius: 1rem;
  border-style: none;
  outline: none;
  cursor: pointer;
  margin: "0 1rem";
}


body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}
ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
